export default {
	data() {
		return {
			globalData: {},
			order_count: 0,
			cart_count: 0,
			userInfo: {}
		};
	},
	onLoad: function(option) {
		this.globalData = getApp().globalData;
		this.userInfo = this.globalData.userInfo
		this.loadData();
		if (option.order) {
			this.isOrder = true;
		}
	},
	methods: {
		wxGetUserInfo() {
			let that = this;
			
			let UserInfo = uni.getStorageSync('getUserInfo');
			if(UserInfo){
				that.$minApi
					.login(UserInfo)
					.then(res => {
						that.userInfo = res.data
						getApp().globalData.userInfo = res.data
						uni.setStorageSync('access_token', res.token);
					})
					.catch(err => {
						console.log(err);
					});
				return true;
			}
			
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					uni.setStorageSync('getUserInfo', infoRes);
					that.$minApi
						.login(infoRes)
						.then(res => {
							that.userInfo = res.data
							getApp().globalData.userInfo = res.data
							uni.setStorageSync('access_token', res.token);
						})
						.catch(err => {
							console.log(err);
						});
				},
				fail(res) {}
			});
		},
		loadData() {
			this.$minApi
				.userInfo({})
				.then(res => {
					this.userInfo = res;
				})
				.catch(err => {
					console.log(err);
				});
			this.$minApi
				.orderCount({})
				.then(res => {
					this.order_count = res;
				})
				.catch(err => {
					console.log(err);
				});
			this.$minApi
				.cartCount({})
				.then(res => {
					this.cart_count = res;
				})
				.catch(err => {
					console.log(err);
				});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target;
		},
		hideModal(e) {
			this.modalName = null;
		},
		Gridchange(e) {
			this.gridCol = e.detail.value;
		},
		Gridswitch(e) {
			this.gridBorder = e.detail.value;
		},
		MenuBorder(e) {
			this.menuBorder = e.detail.value;
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value;
		},
		MenuCard(e) {
			this.menuCard = e.detail.value;
		},
		SwitchSex(e) {
			this.skin = e.detail.value;
		},

		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX;
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left';
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target;
			} else {
				this.modalName = null;
			}
			this.listTouchDirection = null;
		}
	}
};
